  import Buddly from 'bubbly-bg'

  export default {
  name: 'app',
  mounted(){
    if(innerWidth>innerHeight){
      document.getElementById('app').style.width="50vh";
    }
    bubbly({
      colorStart: "#4c004c",
      colorStop: "#1a001a",
      bubbleFunc: () => `hsla(${Math.random() * 360}, 100%, 50%, ${Math.random() * 0.25})`
    });
  }
}



